import fs from "fs"
import https from "https"
export interface ICertificateInfo {
  name: string;
  content?: string;
  isValid: boolean;
}
export default class CaHelper {
  private static readonly certificateExtentions: Record<string, true | undefined> = { "crt": true, "crl": true, "csr": true, "der": true, "cer": true, "ca-bundle": true, "p7b": true, "p7c": true, "p7s": true, "pem": true, "arm": true, "pfx": true, };
  public static getAllFiles(dir: string) {
    if (!fs.existsSync(dir)) { return undefined };
    
    const files: string[] = fs.readdirSync(dir);
    if (!files) return undefined;
    
    const certList: Array<ICertificateInfo> = [];
    files.forEach(file => {
      let fileExtention = file.split(".").pop();
      const name = dir + '/' + file;
      if (!fileExtention || !CaHelper.certificateExtentions[fileExtention]) { certList.push({ isValid: false, name }); return; }

      certList.push({ name, content: fs.readFileSync(name, { encoding: "utf-8" }), isValid: true });
    });
    return (certList.length == 0) ? undefined : certList;
  }

  public static registerCaCerts(caCerts: Array<string>) {
    if (caCerts.length == 0) { return };

    https.globalAgent.options.ca = https.globalAgent.options.ca || [];

    if (!(https.globalAgent.options.ca instanceof Array)) {
      https.globalAgent.options.ca = [https.globalAgent.options.ca];
    }

    let certificateList = https.globalAgent.options.ca;
    caCerts?.forEach(caCert => certificateList.push(caCert))
  }
}